$primary: #ffffff;
$dark: #242424;
$blue: #276afb;
$red: #f00946;
$green: #25ce4a;

.btn {
  padding: 8px 20px;
  border-radius: 4px;
  outline: none;
  border: none;
  cursor: pointer;
  white-space: nowrap;
}

.btn--primary {
  background-color: $primary;
  color: $dark;
  border: 1px solid $primary;
}

.btn--outline {
  background-color: transparent;
  padding: 8px 20px;
  color: $primary;
  border: 1px solid $primary;
  transition: all 0.3s ease-out;
}

.btn--medium {
  padding: 8px 20px;
  font-size: 18px;
}

.btn--large {
  padding: 12px 26px;
  font-size: 20px;
}

.btn--mobile {
  text-align: center;
  border-radius: 4px;
  width: 80%;
  text-decoration: none;
  font-size: 24px;
  background-color: transparent;
  color: $primary;
  padding: 14px 20px;
  border: 1px solid $primary;
  transition: all 0.3s ease-out;
}

.btn--large,
.btn--medium,
.btn--mobile {
  &:hover {
    transition: all 0.3s ease-out;
    background-color: $primary;
    color: $dark;
  }
}

.btn--wide {
  padding: 12px 64px;
  font-size: 20px;
  color: #6ee6fc;
  background-color: rgb(31, 31, 34);
  box-shadow: -2px -2px 6px rgba(255, 255, 255, 0.1),
    2px 2px 6px rgba(0, 0, 0, 0.8);
  border: none;
  border-radius: 20px;

  &:hover {
    box-shadow: inset -2px -2px 6px rgba(255, 255, 255, 0.1),
      inset 2px 2px 6px rgba(0, 0, 0, 0.8);
    transition: all 0.3s ease-out;
  }

  @media screen and (max-width: 768px) {
    padding: 12px 32px;
  }
}

.primary {
  background-color: $dark;
  color: $primary;
  border: none;
  &:hover {
    transition: all 0.2s ease-out;
    background-color: $primary;
    color: $dark;
  }
}

.btn-link {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  padding: 8px 16px;
  height: 100%;
  width: 100%;
}
